import React, { Component } from "react";
import AppHeader from "../AppHeader/AppHeader";
import PostAddForm from "../PostAddForm/PostAddForm";
import PostList from "../PostList/PostList";
import PostStatusFilter from "../PostStatusFilter/PostStatusFilter";
import SearchPanel from "../SearchPanel/SearchPanel";

// import '../../css/app.css'
// import '../../css/app-header.css'
import '../../css/index.css'
import '../../css/post-add-form.css'
import '../../css/post-list-item.css'
import '../../css/post-list.css'
import '../../css/post-status-filter.css'
import '../../css/search-panel.css'

import styled from "styled-components";

const AppBlock = styled.div`
      margin: 0 auto;
      max-width: 800px;
`


export default class  App extends Component  {

  constructor(props) {
    super(props)
    this.state = {
       data : [
        {label: 'Чё зыришь глаза пузыришь?', important: false, like: false, id: 1},
        {label: 'Классика Hello world', important: false, like: false, id: 2},
        {label: 'Дороу!!!!', important: false, like: false, id: 3},
      ],

      term: '',
      filter: 'all'
    };

    this.deleteItem = this.deleteItem.bind(this);
    this.addItem = this.addItem.bind(this);
    this.onToggleImportant = this.onToggleImportant.bind(this);
    this.onToggleLike = this.onToggleLike.bind(this);
    this.onUpdateSearch = this.onUpdateSearch.bind(this);
    this.onFilterSelect = this.onFilterSelect.bind(this);

    this.maxId = 5;
  }

  deleteItem(id) {
    this.setState(({data}) => {
      const index = data.findIndex(elem => elem.id === id);

      const before = data.slice(0, index);
      const after = data.slice(index + 1);

      const newArr = [...before, ...after];

      return {
        data: newArr
      }
    })
  }

  addItem(body) {
    const newItem = {
      label: body,
      import: false,
      id: this.maxId++
    }

    this.setState(({data}) => {
      const newArr = [...data, newItem];

      return {
        data: newArr
      }
    })
  }

  onToggleImportant(id) {
    this.setState(({data}) => {
      const index = data.findIndex(elem => elem.id === id);

      const old = data[index];
      const newItem = {...old, important: !old.important};

      const before = data.slice(0, index);
      const after = data.slice(index + 1);

      const newArray = [...before, newItem, ...after];

      return{
        data: newArray
      }
    })
  }

  onToggleLike(id) {
    this.setState(({data}) => {
      const index = data.findIndex(elem => elem.id === id);

      const old = data[index];
      const newItem = {...old, like: !old.like};

      const before = data.slice(0, index);
      const after = data.slice(index + 1);

      const newArray = [...before, newItem, ...after];

      return{
        data: newArray
      }
    })
  }

  searchPost(items, term) {
    if (term.length === 0) {
      return items
    }

    return items.filter( (item) => {
      return item.label.indexOf(term) > -1
    });
  } 


  filterPost(items, filter) {
    if (filter === 'like') {
      return items.filter(item => item.like)
    } else {
      return items
    }
  }

  onUpdateSearch(term) {
    this.setState({term})
  }

  onFilterSelect(filter) {
    this.setState({filter})
  }
  

  render() {

    const {data, term, filter} = this.state;

    const liked = this.state.data.filter(item => item.like).length;

    const allPosts = data.length;

    const visiblePosts = this.filterPost(this.searchPost(data, term), filter);


    return (
      <AppBlock>
        <AppHeader 
        liked={liked}
        allPosts={allPosts}
        />
  
        <div className="search-panel d-flex">
          <SearchPanel 
          onUpdateSearch={this.onUpdateSearch}
          />
          <PostStatusFilter 
          filter={filter}
          onFilterSelect={this.onFilterSelect}
          />
        </div>
  
        <PostList posts={visiblePosts} 
        onDelete={this.deleteItem}
        onToggleImportant={this.onToggleImportant}
        onToggleLike={this.onToggleLike}
        />
        <PostAddForm 
        onAdd={this.addItem}
        />
      </AppBlock>
    )
  }
};


